p {
  margin: 0;
  line-height: 165%;
}

td {
  line-height: 140%;
}

a {
  color: #7c6eb0;
}

iframe {
  outline: none;
  border: none;
}

li {
  line-height: 140%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  line-height: 145%;
  font-weight: normal;
}
h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  display: inline-block;
  color: #3aadc6;
  text-decoration: none;
  line-height: 130%;
}

.row {
  position: relative;
  margin: 0 auto;
  padding: 0 15px;
  width: 100%;
}
.row.full {
  max-width: none;
}
@media only screen and (min-width: 1261px) {
  .row {
    max-width: 1240px;
  }
  .row.full {
    padding: 0 4vw;
  }
}
body {
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  color: #7c6eb0;
}
body.mob-menu-open {
  overflow: hidden;
}
@media only screen and (min-width: 641px) {
  body {
    font-size: 16px;
  }
}
@media only screen and (min-width: 1025px) {
  body {
    font-size: 18px;
  }
}

img {
  pointer-events: none;
}

#wpadminbar {
  display: none;
}

.main-wrapper {
  overflow: hidden;
}

.step-title {
  margin: 0 0 35px;
  text-align: center;
  font-family: "Neucha", cursive;
  font-size: 36px;
  line-height: 100%;
}
@media only screen and (min-width: 641px) {
  .step-title {
    font-size: 44px;
  }
}
@media only screen and (min-width: 1025px) {
  .step-title {
    margin: 0 0 60px;
    font-size: 56px;
  }
}

.styled-title {
  font-family: "Movskate", sans-serif;
}

.text-wrap {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.bg-pattern {
  position: relative;
}
.bg-pattern:after {
  content: "";
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(../../img/bg-pattern.png) no-repeat center/cover;
}

.button {
  position: relative;
  display: inline-block;
  padding: 12px 15px;
  border-radius: 8px;
  text-decoration: none;
  font-family: "Neucha", cursive;
  text-transform: uppercase;
  text-align: center;
}
.button:hover {
  top: 1px;
}
.button:hover:after {
  bottom: -4px;
}
.button.rose {
  background: #d33c68;
  color: #ffffff;
}
.button.rose:after {
  background: url(../../img/button/rose/border.png) no-repeat center/100% 100%;
}
.button.yellow {
  background: #FBF266;
  color: #7c6eb0;
}
.button.yellow:after {
  background: url(../../img/button/yellow/border.png) no-repeat center/100% 100%;
}
.button:after {
  content: "";
  position: absolute;
  bottom: -5px;
  left: -1px;
  width: 100%;
  height: 100%;
}

.video iframe,
.video .img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.video .img {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}
.video .img .play {
  background: red;
  border-radius: 50%/10%;
  color: #FFFFFF;
  font-size: 16px;
  height: 3em;
  margin: 20px auto;
  padding: 0;
  position: relative;
  text-align: center;
  text-indent: 0.1em;
  transition: all 150ms ease-out;
  width: 4em;
}
.video .img .play:before, .video .img .play:after {
  content: "";
  position: absolute;
}
.video .img .play:before {
  top: 9%;
  left: -5%;
  right: -5%;
  bottom: 9%;
  background: inherit;
  border-radius: 5%/50%;
}
.video .img .play:after {
  top: 50%;
  border-style: solid;
  border-width: 1em 0 1em 1.732em;
  border-color: transparent transparent transparent #fff;
  height: 0;
  width: 0;
  margin: -1em 0 0 -0.75em;
  font-size: 0.75em;
}
.video .img img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.fixed-social {
  z-index: 999;
  position: fixed;
  right: 7px;
  bottom: 7px;
  display: flex;
  flex-direction: column;
  gap: 2px;
}
@media only screen and (min-width: 641px) {
  .fixed-social {
    right: 20px;
    bottom: 15px;
    gap: 5px;
  }
}
.fixed-social a {
  width: 34px;
  display: flex;
  border-radius: 50%;
  border: 2px solid #fff;
}
@media only screen and (min-width: 641px) {
  .fixed-social a {
    width: 40px;
    border: 3px solid #fff;
  }
}
.fixed-social a img {
  width: 100px;
  height: 100%;
  object-fit: cover;
}

header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
  background: #fff;
}
.mob-menu-open header {
  z-index: 1001;
}
header:after {
  content: "";
  position: absolute;
  top: calc(100% - 1px);
  left: 0;
  width: 100%;
  height: 1vw;
  background: url(../../img/waves/bottom-1.svg) no-repeat center/100% 100%;
}
header .wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
header .logo {
  z-index: 2;
  display: block;
  position: relative;
  margin: 5px 0 -15px;
  background: #fff;
  border-radius: 50%;
  padding: 2px;
  height: 65px;
  width: 65px;
  text-decoration: none;
}
@media only screen and (min-width: 1025px) {
  header .logo {
    padding: 6px;
    height: 105px;
    width: 105px;
    margin: 5px 0 -50px;
  }
}
header .menu-button {
  z-index: 2;
  width: 40px;
  height: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 4px;
  padding: 0 8px;
  margin: 0 36px 0 0;
  background: #7c6eb0;
  border-radius: 50%;
}
.mob-menu-open header .menu-button {
  background: #fff;
  gap: 0;
}
.mob-menu-open header .menu-button span {
  background: #7c6eb0;
}
.mob-menu-open header .menu-button span:nth-child(1) {
  display: none;
}
.mob-menu-open header .menu-button span:nth-child(2) {
  transform: rotate(45deg);
  margin: 0 0 -2px 0;
}
.mob-menu-open header .menu-button span:nth-child(3) {
  transform: rotate(-45deg);
}
@media only screen and (min-width: 1025px) {
  header .menu-button {
    display: none;
  }
}
header .menu-button span {
  width: 100%;
  height: 2px;
  background: #fff;
}
@media only screen and (max-width: 1025px) {
  header .menu {
    z-index: 1;
    display: none;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
    position: absolute;
    left: 0;
    top: 0;
    background: #7c6eb0 url(../../img/bg/step-3.svg) no-repeat center/cover;
    width: 100%;
    height: 100vh;
    padding: calc(15vh + 45px) 0 0 15px;
  }
  header .menu:after {
    content: "";
    pointer-events: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url(../../img/bg-pattern-menu.png) no-repeat center/cover;
  }
  .mob-menu-open header .menu {
    display: flex;
  }
}
header .menu a {
  border-radius: 20px;
  padding: 7px 16px;
  text-decoration: none;
  font-weight: 700;
  font-size: 14px;
  color: #7c6eb0;
}
@media only screen and (max-width: 1025px) {
  header .menu a {
    color: #fff;
    font-size: 18px;
  }
}
@media only screen and (min-width: 1025px) {
  header .menu a {
    padding: 6px 15px 8px;
  }
}
header .menu a.active {
  background: #FBF266;
  color: #7c6eb0;
}
.full-version header .menu a:hover {
  background: #FBF266;
  color: #7c6eb0;
}
header .lang {
  z-index: 2;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 13px;
  font-weight: 700;
  list-style: none;
}
header .lang li {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 0 2px 1px;
  width: 35px;
  height: 35px;
  border-radius: 50%;
}
header .lang li a {
  display: block;
  text-decoration: none;
  color: #88888a;
}
.mob-menu-open header .lang li a {
  color: #fff;
}
header .lang li.current-lang {
  background: #FBF266;
}
header .lang li.current-lang a {
  color: #7c6eb0;
}

footer {
  position: relative;
  background: #fff;
}
footer:before {
  content: "";
  position: absolute;
  bottom: calc(100% - 1px);
  left: 0;
  width: 100%;
  height: 1vw;
  background: url(../../img/waves/top-5.svg) no-repeat center/100% 100%;
}
footer .wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 25px;
  padding: 45px 0 30px;
}
@media only screen and (min-width: 641px) {
  footer .wrap {
    padding: 80px 0 60px;
